import React from "react"
import { Link, StaticQuery, graphql } from "gatsby"
import Image from "gatsby-image"

import { rhythm } from "../utils/typography"

function Bio() {
  return (
    <StaticQuery
      query={bioQuery}
      render={data => {
        const { author, social } = data.site.siteMetadata
        return (
          <div
            style={{
              display: `flex`,
            }}
          >
            <Image
              fixed={data.avatar.childImageSharp.fixed}
              alt={author}
              quality={100}
              style={{
                marginRight: rhythm(1 / 2),
                marginBottom: 0,
                borderRadius: `100%`,
                minWidth: 50
              }}
              imgStyle={{
                borderRadius: `50%`,
              }}
            />
            <p>
              Hey, I'm <strong>{author}</strong>. I love building software, mixing stacks and making memes.
              <br></br>
              <a href={`https://twitter.com/${social.twitter}`} target="blank">
                Twitter
              </a> &middot; <a href={`https://github.com/${social.twitter}`}  target="blank">
                GitHub
              </a> &middot; <a href={`https://linkedin.com/in/${social.twitter}`}  target="blank">
                LinkedIn
              </a> &middot; <a href={`https://facebook.com/${social.twitter}`}  target="blank">
                Facebook
              </a>
            </p>
          </div>
        )
      }}
    />
  )
}

const bioQuery = graphql`
  query BioQuery {
    avatar: file(absolutePath: { regex: "/profile-pic.jpg/" }) {
      childImageSharp {
        fixed(width: 50, height: 50, quality: 100) {
          ...GatsbyImageSharpFixed
        }
      }
    }
    site {
      siteMetadata {
        author
        social {
          twitter
        }
      }
    }
  }
`

export default Bio
